import React from "react";
import TW from "./st.module.css"
//@ts-ignore
import {  Container,Display4, Button, Row, Col } from 'bootstrap-4-react';
import {Redirect, Route} from "react-router-dom";
const rowStyle = {
    marginTop: '5%',
    justifyContent : 'center'
};
const GoHome = () => {
   return <Redirect to={"Home/"}/>
}

const TilesGameOverMenu : React.FC<{}> = () =>{
    return <Container>
            <Row    justifyContent="center" className={TW.GameOverText}>
                <Display4 display="flex"
                          text="white">
                    Congratulations you won!!!
                </Display4>
            </Row>
                <Row style={rowStyle}  justifyContent="center" >
                        <Button  success >Try again ?</Button>
                        <Button onClick={GoHome} className="ml-5" info as="a" href="#">Go home</Button>
                </Row>

        </Container>
}
export default TilesGameOverMenu