import React, { Component } from 'react';
import Bootstrap, { Carousel, BImg } from 'bootstrap-4-react';
import Forest1 from "../../utils/imgs/bg1.jpg"
import Forest2 from "../../utils/imgs/bg2.jpg"
import Forest3 from "../../utils/imgs/bg3.jpg"
import Forest4 from "../../utils/imgs/bg4.jpg"
import Forest5 from "../../utils/imgs/bg5.jpg"
import SS from "./SS.module.css"

export default class Slider extends Component {

    componentWillMount() {
        window.setTimeout(() => Bootstrap.carousel('#carouselExampleFade'), 2000);
    }

    render() {
        return (
            <Carousel w="100" id="carouselExampleFade" fade>
                <Carousel.Inner>
                    <Carousel.Item active>
                        <img className={SS.Images} src={Forest1} alt=""/>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img className={SS.Images} src={Forest2} alt=""/>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img className={SS.Images} src={Forest3} alt=""/>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img className={SS.Images} src={Forest4} alt=""/>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img className={SS.Images} src={Forest5} alt=""/>
                    </Carousel.Item>

                </Carousel.Inner>
                <Carousel.Prev href="#carouselExampleFade">
                    <Carousel.Prev.Icon />
                </Carousel.Prev>
                <Carousel.Next href="#carouselExampleFade">
                    <Carousel.Next.Icon />
                </Carousel.Next>
            </Carousel>
        )
    }
}