import React from "react";
import {NavLink} from "react-router-dom";
import logo from "../../utils/imgs/react_logo.png"
import HS from "./HS.module.css"
//@ts-ignore
import {Navbar, Nav, Collapse, Row, Col} from 'bootstrap-4-react';
/*
* className={`${d.item} ${d.active}`}*/

const Header = () => {
    return (
        <Navbar expand="lg" dark bg="dark" mb="0">
            <Navbar.Brand href="#">
                <img src={logo} width="60" height="40" className="d-inline-block align-top" alt="logo"/>
            </Navbar.Brand>
            <Navbar.Toggler target="#navbarColor1"/>
            <Collapse navbar id="navbarColor1">
                <Navbar.Nav mr="auto">
                    <Nav.ItemLink >
                        <div className={`${HS.item} ${HS.active}`}>
                            <NavLink to="/Home/">
                                Home
                            </NavLink>
                        </div>
                    </Nav.ItemLink>
                    <Nav.ItemLink >
                        <div className={`${HS.item} ${HS.active}`}>
                            <NavLink to="/AboutMe/">
                                About me
                            </NavLink>
                        </div>
                    </Nav.ItemLink>
                    <Nav.ItemLink >
                        <div className={`${HS.item} ${HS.active}`}>
                            <NavLink to="/game/">
                                Bored?
                            </NavLink>
                        </div>
                    </Nav.ItemLink>
                </Navbar.Nav>
            </Collapse>
        </Navbar>
    )
}

export default Header