import React from "react";
import CardBacKSide from "../../utils/imgs/card_back_side.jpg"
import RedCard from "../../utils/imgs/react.png"
import GreenCard from "../../utils/imgs/angular.png"

import TW from "./st.module.css"
import { TSetClickedTiles} from "./TilesContainer";


type TTileProps = {
    CurrentColor : string
    CurrentID : number
    SetClickedTiles : TSetClickedTiles
    ClickedTiles : Array<number>
}
const Tile : React.FC<TTileProps> = (props ) => {


    const onCardClick =() =>{
        props.SetClickedTiles(props.CurrentID,true)
    }


    return  <div>
        <div className={TW.TilesWrapper}>
            {
                props.ClickedTiles.indexOf(props.CurrentID) !== -1
                    ?
                    <img  className={TW.Card} src={
                        props.CurrentColor === "red" ?
                            RedCard
                            : props.CurrentColor === "green" ?
                            GreenCard
                            : undefined
                    } alt=""/>
                : <img  onClick={props.ClickedTiles.length < 2 ? onCardClick : undefined} className={TW.CardBackSide} src={CardBacKSide} alt=""/>
            }

        </div>
    </div>

}
export default Tile